/* Open Sans */
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap');

/* Helvetica Now */
@font-face {
  font-family: 'Helvetica Now Display XBlk';
  src: url('../assets/fonts/HelveticaNowDisplayXBlk.otf') format('opentype');
}


html {font-size: 112.5%;} /*18px*/

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  line-height: 1.75;
  letter-spacing: -0.03rem;
  color: #292929;
  font-size: 18px;

}

p {margin-bottom: 1rem;}

h2, h3, h4, h5 {
  margin: 3rem 0 3rem;
  font-family: 'Helvetica Now Display XBlk', sans-serif;
  font-weight: 100;
  letter-spacing: -.05rem;
}

h1 {
  font-size: 13rem;
  font-weight: 400;
  font-family: 'Helvetica Now Display XBlk', sans-serif;
  letter-spacing: -.4rem;
  color: black;
}

h2 {font-size: 6.854rem;}

h3 {
  font-size: 4.236rem;
}

h4 {
  font-size: 2rem;
  line-height: 1.2;
  color: rgb(29, 29, 29);
}

h5 {
  font-size: 1.618rem;
  color: rgb(29, 29, 29);
}

small, .text_small {font-size: 0.618rem;}